.MessageRequest {
    background-color: #282c34;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 20px 0px 25px 0px;
    /* border-style: solid;
    border-color: #15161e; */
}

.MessageResponse {
    background-color: #383E4C;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 20px 0px 25px 0px;
    /* border-style: solid;
    border-color: #15161e; */
}

.MessageResponse > p {
    font-family: 'Coming Soon';
    font-size: 12px;
    line-height: 2.3;
    border-radius: 10px;
    letter-spacing: 0.08rem;
    /* font-weight: 600; */
}

.MessageParagraph {
    display: flex;
    text-align: left;
    align-items: flex-start;
    width: 55%;
    min-width: 520px;
    white-space: pre-wrap;
    font-family: 'Coming Soon';
    font-size: 12px;
    line-height: 2.3;
    border-radius: 10px;
    letter-spacing: 0.08rem;
    margin: 0px 0px 0px 20px; 
    padding: 0px 0px 3px 0px; 
    /* border-style: solid; */
    border-color: wheat;
    overflow-x: auto;
    /* border-style: solid; */
}

.MessageParagraph::-webkit-scrollbar {
    height: 6px;
}
.MessageParagraph::-webkit-scrollbar-track { 
    -webkit-border-radius: 10px;
    border-radius: 10px;
    background: #282c34;
}

.MessageParagraph::-webkit-scrollbar-thumb {
    -webkit-border-radius: 10px;
    border-radius: 10px;
    background: #8d929e; 
}


.Avatar {
    box-shadow: 0 0 10px #15161e;
}

@media only screen and (max-width: 1100px) {
    .MessageParagraph {
        width: 51%;
        min-width: 450px;
    }
}

@media only screen and (max-width: 860px) {
    .MessageParagraph {
        width: 80%;
        min-width: 0px;
    }
    .MessageRequest {
        padding: 25px 0px 30px 0px;
    }
    .MessageResponse {
        padding: 25px 0px 30px 0px;
    }
}