.sidebar {
    background-color: #1B1F23;
    position: absolute;
    left: 0;
    top: 0;
    height: 100vh;
    min-width: 250px;
    align-items: center;
    text-align: left;
    padding: 0px 0px 0px 10px;
}

.sidebar > p {
    color: white;
    font-family: 'Inter';
    font-size: 11px;
}

.newChat {
    padding: 9px 0px 9px 15px;
    margin: 10px 10px 5px 50px;
    background-color: #1B1F23;
    border-style: solid;
    border-width: 1px;
    border-color: aqua;
    border-radius: 5px;
    width: 75%;
    color: white;
    font-family: 'Coming Soon';
}


.newChat:hover {
    background-color: #2d3238;
}

